html {
    scroll-behavior: smooth;
}
body {
    background-color: #1a1142;
}
.main.index {
    position: relative;
    background-color: #1a1142;
    overflow: hidden;
}
.main.index::before {
    content: "";
    position: absolute;
    top: 470px;
    left: calc(50% - 300px);
    width: 630px;
    height: 630px;
    filter: blur(1200px);
    width: 600px;
    height: 600px;
    background-color: #e5a7d0;
    opacity: 0.9;
}
.main.presale {
    background-color: #1a1142;
    overflow: hidden;
}
@media all and (min-width: 1025px) {
    .header.presale .header__body {
        justify-content: center;
        position: relative;
    }
    .header.presale .header__button {
        position: absolute;
        right: 0;
    }
}
@media all and (max-width: 1024px) {
    .header.presale .header__body {
        flex-direction: column;
        align-items: center;
    }
    .header.presale .header__logo {
        margin-bottom: 40px;
    }
    .header.presale .header__button {
        display: block;
    }
}
.header .header__body {
    display: flex;
    max-width: 1350px;
    margin: 0 auto;
    padding-top: 20px;
}
.header .header__logo {
    display: flex;
    align-items: center;
}
.header .header__logo .image {
    width: 46px;
    height: auto;
}
.header .header__logo-image {
    position: relative;
}
.header .header__logo-image img {
    min-width: 194px;
    max-width: 194px;
    height: auto;
}
.header .header__logo-image::before {
    content: "";
    width: 80px;
    height: 80px;
    position: absolute;
    top: -20px;
    left: -20px;
    background: radial-gradient(
        46.06% 46.06% at 50% 50%,
        #3d2796 0%,
        rgba(63, 40, 154, 0) 100%
    );
    opacity: 0.39;
}
.header .header__nav-items {
    display: flex;
}
.header .header__nav-item {
    margin: 0 15px;
}
.header .header__nav-item a {
    color: #fff;
    text-decoration: none;
    font-family: "Inter", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
    opacity: 0.9;
}
.header .header__languages {
    position: relative;
}
.header .header__languages-icon {
    display: flex;
    align-items: center;
    font-size: 16px;
    color: #fff;
    font-family: "Inter", sans-serif;
    font-style: normal;
    font-weight: 400;
    cursor: pointer;
    user-select: none;
}
.header .header__languages-icon svg {
    width: 10px;
    height: auto;
    margin-left: 10px;
}
.header .header__languages-icon img {
    height: 12px;
    width: auto;
    display: block;
    margin-left: 10px;
}
.header .header__languages-list {
    position: absolute;
    top: 100%;
    right: 0;
    border-radius: 5px;
    box-sizing: border-box;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.2s ease-in-out;
    padding-top: 12px;
}
.header .header__languages-list.active {
    opacity: 1;
    pointer-events: visible;
    transition: opacity 0.2s ease-in-out;
}
.header .header__languages-list-item {
    text-align: right;
    margin-bottom: 12px;
    box-sizing: border-box;
    font-size: 16px;
    color: #fff;
    font-family: "Inter", sans-serif;
    font-style: normal;
    font-weight: 400;
    cursor: pointer;
    user-select: none;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.header .header__languages-list-item span {
    transition: color 0.2s ease-in-out;
}
.header .header__languages-list-item img {
    height: 12px;
    width: auto;
    display: block;
    margin-left: 5px;
}
.header .header__languages-list-item:hover span {
    color: #fabeff;
    transition: color 0.2s ease-in-out;
}
.header .header__button {
    position: relative;
}
.header .header__button a {
    border: none;
    font-family: "Inter", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 22px;
    opacity: 0.9;
    text-shadow: 0px 10.9012px 25.4362px rgba(23, 13, 36, 0.1);
    color: #fff;
    background: #11aadf;
    transition: all 0.3s ease;
    border-radius: 9.10004px;
    padding: 16px 25px;
    box-sizing: border-box;
    cursor: pointer;
    text-decoration: none;
    white-space: nowrap;
}
.header .header__button a:hover {
    background: rgb(77, 36, 255);
}
.header .header__button::before {
    content: "";
    position: absolute;
    top: -7px;
    left: -53px;
    width: 304px;
    height: 79px;
    opacity: 0;
    transition: opacity 0.4s ease-in-out;
    background: linear-gradient(90.3deg, #22c7ff 2.07%, #ed1eff 95.15%);
    filter: blur(66px);
}
.header .header__button:hover::before {
    opacity: 0.3;
    transition: opacity 0.4s ease-in-out;
}
@media all and (min-width: 1025px) {
    .header .header__body {
        justify-content: space-between;
        align-items: center;
        padding-bottom: 80px;
    }
}
@media all and (max-width: 1024px) {
    .header .header__body {
        justify-content: space-between;
        align-items: center;
        margin-bottom: 40px;
    }
    .header .header__nav {
        display: none;
    }
    .header .header__button {
        display: none;
    }
}
.top-block {
    padding: 0 20px;
    box-sizing: border-box;
    overflow: hidden;
    background-position: center center;
    background-repeat: no-repeat;
}
.top-block .top-block__body {
    max-width: 1136px;
    margin: 0 auto;
}
.top-block .top-block__top {
    display: flex;
    align-items: center;
}
.top-block .top-block__left-title {
    font-family: "Inter", sans-serif;
    font-style: normal;
    opacity: 0.9;
    font-weight: bold;
    margin-bottom: 55px;
    background: linear-gradient(
        90.45deg,
        #ffffff 1.06%,
        #fabeff 78.32%,
        #f8adff 86.9%,
        #ffc2af 101.21%
    );
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    letter-spacing: -3px;
    transform: perspective(15em) rotateX(25deg);
}
.video-instructions {
    padding-top: 30px;
    font-family: "Inter", sans-serif;
    font-size: 19px;
    display: flex;
    align-items: center;
    gap: 50px;
}

.video-instructions a {
    max-width: 300px;
    color: #fff;
    z-index: 10;
}

.video__link {
    display: flex;
    gap: 40px;
    align-items: center;
}

.video__link p,
.video__link path {
    transition: all 0.2s ease;
}

.video__link:hover p {
    color: #40ddff;
}

.video__link:hover path {
    fill: #40ddff;
}

.video__link:hover circle {
    stroke: #40ddff;
}

.mobile-store {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
}

.mobile-store__btn {
    transition: all 0.2s ease;
}

.mobile-store__btn:hover {
    opacity: 0.7;
}

.top-block__buttons {
    margin-top: 60px;
    display: flex;
    justify-content: center;
    gap: 50px;
    flex-wrap: wrap;
}

.top-block__btn {
    position: relative;
    z-index: 5;
    min-width: 250px;
    min-height: 57px;
}

.top-block__btn::before {
    content: "";
    position: absolute;
    top: -7px;
    left: -53px;
    width: 304px;
    height: 29px;
    opacity: 0;
    transition: opacity 0.4s ease-in-out;
    background: linear-gradient(90.3deg, #22c7ff 2.07%, #ed1eff 95.15%);
    filter: blur(66px);
}

.top-block__btn a {
    /* position: absolute; */
    display: flex;
    width: 100%;
    height: 100%;
    border: none;
    font-family: "Inter", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 22px;
    opacity: 0.9;
    text-shadow: 0px 10.9012px 25.4362px rgb(23 13 36 / 10%);
    color: #fff;
    transition: all 0.2s ease;
    border-radius: 9.10004px;
    box-sizing: border-box;
    cursor: pointer;
    text-decoration: none;
    white-space: nowrap;
    align-items: center;
    justify-content: center;
}

.top-block__btn_github a {
    background: #11aadf url("../icons/github.svg") no-repeat 20px center;
}
.top-block__btn_github a:hover {
    background: rgb(77, 36, 255) url("../icons/github.svg") no-repeat 20px
        center;
}

.top-block__btn_file a {
    background: #11aadf url("../icons/document.svg") no-repeat 20px center;
}
.top-block__btn_file a:hover {
    background: rgb(77, 36, 255) url("../icons/document.svg") no-repeat 20px
        center;
}

.top-block__btn:hover::before {
    opacity: 0.3;
    transition: opacity 0.4s ease-in-out;
}

/* .mobile-store__apple {
} */

.mobile-store_mobile {
    z-index: 10;
    display: none;
    margin-bottom: 60px;
    gap: 20px;
}

.top-block .top-block__left-button a {
