    font-family: "Inter", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 27px;
    line-height: 32px;
    letter-spacing: -1.77447px;
    opacity: 0.9;
    text-shadow: 0px 10.9012px 25.4362px rgba(23, 13, 36, 0.1);
