    letter-spacing: -1.77447px;
    opacity: 0.9;
    text-shadow: 0 10.9012px 25.4362px rgba(23, 13, 36, 0.1);
    background: linear-gradient(
        90.45deg,
        #ffffff 1.06%,
        #fabeff 78.32%,
        #f8adff 86.9%,
